import React, { useEffect, useState } from "react";
import background from "../assets/img/bg-general.jpg";
import logo from "../assets/img/logo-Juntos.png";
import logoBase from "../assets/img/logos-base.png";
import hashJuntos from "../assets/img/numeral-juntos-otravez.png";
import { app } from "../database/firebase";
import { Container } from "reactstrap";
const db = app.firestore();
const LandingTwo = (props) => {
  const [campaign, setCampaign] = useState({});
  const { propsCampaign, propsStore } = props.location.state;
  useEffect(() => {
    const fetchCampaign = async () => {
      const dbRef = db.collection("campaigns").doc(propsCampaign);
      const campaignDoc = await dbRef.get();
      setCampaign(campaignDoc.data());
    };

    fetchCampaign();
  }, [propsCampaign]);
  return (
    <Container
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        backgroundImage: `url(${background})`,
        width: "100%",
        backgroundPosition: "center",
        backgroundSize: "100% 100%",
        backgroundRepeat: "no-repeat",
      }}
    >
      <img alt="Logo" src={logo} style={{ padding: 30 }} />
      <img
        alt="hashJuntos"
        className="rounded mx-auto d-block"
        src={hashJuntos}
        width={200}
      />
      <img
        src={campaign.image}
        className="img-fluid mt-3"
        alt="Imagen de descuento"
      />
      <button
        className="btn btn-outline-neutral"
        onClick={() =>
          props.history.push("/landingRegister", { propsCampaign, propsStore })
        }
      >
        RECLAMAR 10% DESCUENTO ADICIONAL
      </button>
      <img alt="logobase" src={logoBase} />
    </Container>
  );
};

export default LandingTwo;
