import React, { useState, useRef, useEffect } from "react";
import {
  Card,
  Row,
  Col,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  CardBody,
  CardTitle,
  CardSubtitle,
  CardImg,
  CardText,
} from "reactstrap";
import { app, analytics } from "../database/firebase";
import NotificationAlert from "react-notification-alert";
import crypto from "crypto";
const db = app.firestore();
const url = "http://52.44.107.140:4000/";

const Campaigns = (props) => {
  let options = {};
  const notificationAlert = useRef(null);
  const [modal, setModal] = useState(false);
  const [showMore, setShowMore] = useState(false);
  const [campaign, setCampaing] = useState({
    name: "",
    descBottom: "",
    stores: [],
    image: "",
    nameImage: "",
  });
  const [stores, setStores] = useState([]);
  const [campaigns, setCampaigns] = useState([]);

  useEffect(() => {
    const fetchStores = async () => {
      const storesCollection = await db.collection("stores").get();
      setStores(
        storesCollection.docs.map((doc) => {
          return { id: doc.id, status: false, ...doc.data() };
        })
      );
    };
    fetchStores();
  }, []);

  useEffect(() => {
    const fetchCampaigns = async () => {
      const campaignsCollection = await db.collection("campaigns").get();
      setCampaigns(
        campaignsCollection.docs.map((doc) => {

          return { id: doc.id, ...doc.data() };
        })
      );
    };
    fetchCampaigns();
  }, []);

  const toggle = () => setModal(!modal);
  const onSubmit = async (event) => {
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se debe llenar los datos</div>
        </div>
      ),
      type: "danger",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    event.preventDefault();
    const imageNameRandom = crypto.randomBytes(20).toString("hex");
    const idCampaignsRandom = crypto.randomBytes(20).toString("hex");
    const newCampaigns = campaign;
    newCampaigns.id = idCampaignsRandom;

    const storesCampaign = stores.filter((item) => {
      if (item.status) {
        item.qr = `https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=${url}/landing/${newCampaigns.id}/${item.id}`;
        return item;
      }
      return null;
    });
    const storageRef = app.storage().ref();
    const fileRef = storageRef.child(imageNameRandom);
    await fileRef.put(campaign.image);
    const imageURL = await fileRef.getDownloadURL();
    newCampaigns.nameImage = fileRef.name;
    newCampaigns.stores = storesCampaign;
    newCampaigns.image = imageURL;
    if (
      newCampaigns.name === "" ||
      newCampaigns.descBottom === "" ||
      newCampaigns.stores.length === 0 ||
      newCampaigns.image === ""
    ) {
      notificationAlert.current.notificationAlert(options);
      return;
    }
    await db.collection("campaigns").add(newCampaigns);
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se creo correctamente la campaña!</div>
        </div>
      ),
      type: "success",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    notificationAlert.current.notificationAlert(options);
    setCampaigns((oldArray) => [...oldArray, newCampaigns]);
    toggle();
  };

  const onDelete = async (e, id) => {
    e.preventDefault();
    const storageRef = app.storage().ref();
    const dbRef = await db.collection("campaigns").where("id", "==", id);
    const campDelete = await dbRef.get();
    const desertRef = storageRef.child(campDelete.docs[0].data().nameImage);
    await desertRef.delete();
    await campDelete.docs[0].ref.delete();
    setCampaigns((oldArray) => [...oldArray.filter((item) => item.id !== id)]);
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se elimino correctamente!</div>
        </div>
      ),
      type: "success",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    notificationAlert.current.notificationAlert(options);
  };

  return (
    <>
      <NotificationAlert ref={notificationAlert} />

      <div className="content">
        <Col className="my-3">
          <Button className="bg-success" onClick={toggle}>
            <i className="nc-icon nc-simple-add mr-3" />
            Agregar Campaña
          </Button>
          {/* <Button className="bg-danger">
            Eliminar
            <i className="nc-icon nc-simple-remove" />
          </Button> */}
        </Col>
        <Row>
          {campaigns.map((item) => (
            <Col sm={3}>
              <Card>
                <CardImg src={item.image} />
                <CardBody>
                  <CardTitle tag="h3">{item.name}</CardTitle>

                  {showMore &&
                    item.stores.map((s) => (
                      <Row className="px-3 mt-3">
                        <Col>
                          <img
                            alt="qrImage"
                            src={s.qr}
                            style={{ height: 100, width: 100 }}
                          />
                        </Col>
                        <Col className="d-flex align-items-center">
                          <CardText>{s.name}</CardText>
                        </Col>
                      </Row>
                    ))}

                  <Button
                    className="bg-danger"
                    onClick={(e) => onDelete(e, item.id)}
                  >
                    Eliminar
                    <i className="nc-icon nc-simple-remove ml-3" />
                  </Button>
                  <Button
                    className="bg-success"
                    onClick={() => setShowMore(!showMore)}
                  >
                    {!showMore ? "Ver Mas" : "Ver Menos"}
                  </Button>
                </CardBody>
              </Card>
            </Col>
          ))}
        </Row>
      </div>
      <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>Registrar Nueva Campaña</ModalHeader>
        <ModalBody>
          <Form>
            <FormGroup>
              <Label>Nombre</Label>
              <Input
                onChange={(e) =>
                  setCampaing({ ...campaign, name: e.target.value })
                }
              />
            </FormGroup>

            <FormGroup>
              <Label>Descripción abajo</Label>
              <Input
                onChange={(e) =>
                  setCampaing({ ...campaign, descBottom: e.target.value })
                }
              />
            </FormGroup>
            <FormGroup>
              <Label>Imagen Landing</Label>

              <div class="custom-file">
                <input
                  type="file"
                  class="custom-file-input"
                  id="inputGroupFile01"
                  aria-describedby="inputGroupFileAddon01"
                  onChange={(e) =>
                    setCampaing({ ...campaign, image: e.target.files[0] })
                  }
                />
                <label class="custom-file-label" for="inputGroupFile01">
                  {campaign.image?.name}
                </label>
              </div>
            </FormGroup>
            <Label>Tiendas</Label>
            <FormGroup style={{ height: 100, overflow: "auto" }}>
              {stores.map((item) => (
                <Col className="px-4">
                  <Label>
                    <Input
                      type="checkbox"
                      onChange={() => {
                        let newStore = stores;
                        newStore = newStore.filter((fil) => {
                          if (fil.id === item.id) {
                            fil.status = !fil.status;
                          }
                          return fil;
                        });
                        setStores(newStore);
                      }}
                    />
                    {/* <input type="checkbox" onC/> */}
                    {item.name}
                  </Label>
                </Col>
              ))}
            </FormGroup>
          </Form>
        </ModalBody>
        <ModalFooter>
          <Button className="bg-success" onClick={onSubmit}>
            Crear
          </Button>
          <Button className="bg-secondary" onClick={toggle}>
            Cancelar
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};

export default Campaigns;
