import React, { useEffect, useState, useRef } from "react";
import background from "../assets/img/bg-general.jpg";
import logo from "../assets/img/logo-Juntos.png";
import logoBase from "../assets/img/logos-base.png";
import hashJuntos from "../assets/img/numeral-juntos-otravez.png";
import { Container, Form, FormGroup, Input, Label } from "reactstrap";
import { app } from "../database/firebase";
import NotificationAlert from "react-notification-alert";
const db = app.firestore();

const LandingRegister = (props) => {
  let options = {};
  const notificationAlert = useRef(null);
  const [user, setUser] = useState({
    name: "",
    birDay: "",
    gender: "",
    email: "",
    phone: "",
    campaign: "",
    store: "",
  });
  const [flagAcep, setFlagAcep] = useState(false);
  const [flagPol, setFlagPol] = useState(false);
  useEffect(() => {});
  const { propsCampaign, propsStore } = props.location.state;
  const onSubmit = async (e) => {
    e.preventDefault();

    const { name, birDay, gender, email, phone } = user;
    user.campaign = propsCampaign;
    user.store = propsStore;
    if (
      name === "" ||
      birDay === "" ||
      gender === "" ||
      email === "" ||
      phone === "" ||
      !flagAcep ||
      !flagPol
    ) {
      options = {
        place: "tr",
        message: (
          <div>
            <div>Se debe llenar los datos</div>
          </div>
        ),
        type: "danger",
        icon: "nc-icon nc-bell-55",
        autoDismiss: 7,
      };
      notificationAlert.current.notificationAlert(options);

      return;
    }
    await db.collection("users").add(user);
    props.history.push("/landingThree", { propsCampaign, propsStore });
  };
  return (
    <>
      <NotificationAlert ref={notificationAlert} />

      <Container
        className="content"
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          height: "100vh",
          maxHeight: "100%",
          backgroundImage: `url(${background})`,
          width: "100%",
          backgroundPosition: "center",
          backgroundSize: "100% 100%",
          backgroundRepeat: "no-repeat",
        }}
      >
        <img alt="Logo" src={logo} style={{ padding: 30 }} />
        <img
          alt="hashJuntos"
          className="rounded mx-auto d-block"
          src={hashJuntos}
          width={200}
        />
        <Label className="mt-3" style={{ textAlign: "center", color: "white" }}>
          ANTES DE CONTINUAR QUEREMOS SABER ALGO MÁS DE TI.
        </Label>
        <Form className="mt-3">
          <FormGroup>
            <Input
              placeholder="NOMBRE COMPLETO"
              style={{
                background: "transparent",
                textAlign: "center",
                color: "white",
              }}
              onChange={(e) => setUser({ ...user, name: e.target.value })}
            />
          </FormGroup>
          <FormGroup>
            <Input
              placeholder="FECHA NACIMIENTO"
              style={{
                background: "transparent",
                textAlign: "center",
                color: "white",
              }}
              type="date"
              onChange={(e) => setUser({ ...user, birDay: e.target.value })}
            />
          </FormGroup>
          <FormGroup>
            <Input
              placeholder="GÉNERO"
              style={{
                background: "transparent",
                textAlign: "center",
                color: "white",
              }}
              onChange={(e) => setUser({ ...user, gender: e.target.value })}
            />
          </FormGroup>
          <FormGroup>
            <Input
              placeholder="CORREO"
              type="email"
              style={{
                background: "transparent",
                textAlign: "center",
                color: "white",
              }}
              onChange={(e) => setUser({ ...user, email: e.target.value })}
            />
          </FormGroup>
          <FormGroup>
            <Input
              placeholder="CELULAR"
              style={{
                background: "transparent",
                textAlign: "center",
                color: "white",
              }}
              onChange={(e) => setUser({ ...user, phone: e.target.value })}
            />
          </FormGroup>
          <div class="custom-control custom-checkbox mt-3">
            <input
              type="checkbox"
              className="custom-control-input"
              id="customCheck1"
              onChange={(e) => setFlagAcep(!flagAcep)}
            />
            <label
              className="custom-control-label text-white"
              for="customCheck1"
            >
              ACEPTO TÉRMINOS Y CONDICIONES
            </label>
          </div>
          <div class="custom-control custom-checkbox mt-3">
            <input
              type="checkbox"
              className="custom-control-input"
              id="customCheck2"
              onChange={() => setFlagPol(!flagPol)}
            />
            <label
              className="custom-control-label text-white"
              for="customCheck2"
            >
              POLÍTICA DE PROTECCIÓN DE DATOS
            </label>
          </div>
        </Form>
        <button className="btn btn-outline-neutral my-2" onClick={onSubmit}>
          RECLAMAR
        </button>
        <img alt="logobase" src={logoBase} />
      </Container>
    </>
  );
};

export default LandingRegister;
