import React, { useEffect, useState } from "react";
import background from "../assets/img/Inicio.jpg";
import { useParams } from "react-router";
import { app, analytics } from "../database/firebase";
const db = app.firestore();

const LandingOne = (props) => {
  const { campaign, store } = useParams();
  const [lecQR, setLecQR] = useState({ campaign: "", store: "" });
  useEffect(() => {
    const fetchCampaign = async () => {
      const campaignID = await db
        .collection("campaigns")
        .where("id", "==", campaign)
        .get();
      const fetchStore = async () => {
        const dbRefS = db.collection("stores").doc(store);
        const campaignDocS = await dbRefS.get();
        setLecQR({
          campaign: campaignID.docs[0].data().name,
          store: campaignDocS.data().name,
        });
      };
      fetchStore();
    };

    fetchCampaign();
  }, [campaign, store]);
  useEffect(() => {
    if (lecQR.store === "" || lecQR.campaign === "") {
      return;
    }
    analytics.logEvent("lector_qr", {
      Campaign: lecQR.campaign,
      Store: lecQR.store,
    });
    console.log("Save!");
  }, [lecQR]);
  return (
    <div
      className="content"
      style={{
        display: "flex",
        backgroundImage: `url(${background})`,
        width: "100%",
        height: "100vh",
        backgroundPosition: "center",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <button
        style={{ marginTop: "70%" }}
        className="btn btn-outline-neutral"
        onClick={() =>
          props.history.push("/landingTwo", {
            propsCampaign: campaign,
            propsStore: store,
          })
        }
      >
        Continuar
      </button>
    </div>
  );
};

export default LandingOne;
