import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch, Redirect } from "react-router-dom";

import "bootstrap/dist/css/bootstrap.min.css";
import "assets/scss/paper-dashboard.scss?v=1.2.0";
import "assets/demo/demo.css";
import "perfect-scrollbar/css/perfect-scrollbar.css";
import Login from "views/Login";
import AdminLayout from "layouts/Admin.js";
import LandingOne from "views/LandingOne";
import LandingTwo from "views/LandingTwo";
import LandingRegister from "views/LandingRegister";
import LandingThree from "views/LandingThree";
const hist = createBrowserHistory();

ReactDOM.render(
  <Router history={hist}>
    <Switch>
      <Route path="/admin" render={(props) => <AdminLayout {...props} />} />
      <Route path="/landing/:campaign/:store" component={LandingOne} />
      <Route path="/landingTwo" component={LandingTwo} />
      <Route path="/landingRegister" component={LandingRegister} />
      <Route path="/landingThree" component={LandingThree} />

      <Route path="/" component={Login} />
    </Switch>
  </Router>,
  document.getElementById("root")
);
