/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from "react";
import { app } from "../database/firebase";
import {
  Card,
  Row,
  Col,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Form,
  FormGroup,
  Input,
  Label,
  CardBody,
  CardTitle,
  CardSubtitle,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

const db = app.firestore();
const Stores = () => {
  let options = {};

  const notificationAlert = useRef(null);
  const [modal, setModal] = useState(false);
  const [store, setStore] = useState({
    name: "",
    direction: "",
  });
  const [stores, setStores] = useState([]);

  // UseEffect
  useEffect(() => {
    const fetchStores = async () => {
      const storesCollection = await db.collection("stores").get();
      setStores(
        storesCollection.docs.map((doc) => {
          return { id: doc.id, ...doc.data() };
        })
      );
    };
    fetchStores();
    // db.collection("stores").onSnapshot((querySnaps) => {
    //   const STORES = [];
    //   querySnaps.docs.forEach((doc) => {
    //     const { name, direction } = doc.data();
    //     STORES.push({ id: doc.id, name, direction });
    //   });
    //   setStores(STORES);
    // });
  }, []);

  // Toggle
  const toggle = () => setModal(!modal);
  // OnSubmit
  const onSubmit = async (event) => {
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se debe llenar los datos</div>
        </div>
      ),
      type: "danger",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    event.preventDefault();
    if (store.name === "" || store.direction === "") {
      notificationAlert.current.notificationAlert(options);
      return;
    }
    await db.collection("stores").add(store);
    toggle();
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se creo correctamente la tienda!</div>
        </div>
      ),
      type: "success",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    notificationAlert.current.notificationAlert(options);
    setStores((oldArray) => [...oldArray, store]);
  };

  const onDelete = async (e, id) => {
    e.preventDefault();
    const dbRef = db.collection("stores").doc(id);
    await dbRef.delete();
    setStores((oldArray) => [...oldArray.filter((item) => item.id !== id)]);
    options = {
      place: "tr",
      message: (
        <div>
          <div>Se elimino correctamente!</div>
        </div>
      ),
      type: "success",
      icon: "nc-icon nc-bell-55",
      autoDismiss: 7,
    };
    notificationAlert.current.notificationAlert(options);
  };

  return (
    <>
      <NotificationAlert ref={notificationAlert} />

      <div className="content">
        <Col className="my-3">
          <Button className="bg-success" onClick={toggle}>
            <i className="nc-icon nc-simple-add mr-3" />
            Agregar Tienda
          </Button>
          {/* <Button className="bg-danger">
            Eliminar
            <i className="nc-icon nc-simple-remove" />
          </Button> */}
        </Col>
        <Row>
          {stores.map((item) => (
            <Col sm={3}>
              <Card>
                <CardBody>
                  <img
                    src={require("assets/img/store.png")}
                    alt="Imagen de tienda"
                  />
                  <CardTitle tag="h5">{item.name}</CardTitle>
                  <CardSubtitle tag="h6">{item.direction}</CardSubtitle>
                  <Button
                    className="bg-danger"
                    onClick={(e) => onDelete(e, item.id)}
                  >
                    Eliminar
                    <i className="nc-icon nc-simple-remove ml-3" />
                  </Button>
                </CardBody>
              </Card>
            </Col>
          ))}
        </Row>
      </div>
      <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>Registrar Nueva Tienda</ModalHeader>
        <ModalBody>
          <Form>
            <FormGroup>
              <Label>Nombre</Label>
              <Input
                onChange={(e) => setStore({ ...store, name: e.target.value })}
              />
            </FormGroup>
            <FormGroup>
              <Label>Dirección</Label>
              <Input
                onChange={(e) =>
                  setStore({ ...store, direction: e.target.value })
                }
              />
            </FormGroup>
          </Form>
        </ModalBody>
        <ModalFooter>
          <Button className="bg-success" onClick={onSubmit}>
            Crear
          </Button>
          <Button className="bg-secondary" onClick={toggle}>
            Cancelar
          </Button>
        </ModalFooter>
      </Modal>
    </>
  );
};

export default Stores;
